.popup__container {
	position: relative;
	display: flex;
	flex-direction: column;
	margin: auto;
	max-width: 430px;
	width: 100%;
	height: 100%;
	background: #fff;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
	border-radius: 10px;
	padding: 34px 36px 36px 36px;
	box-sizing: border-box;
}

@media screen and (max-width: 568px) {
	.popup__container {
		max-width: 282px;
		padding: 24px 22px;
	}
}
